 to be carried as a personal companion to training, modular so it could be cheaply fixed and to limit its environmental impact. It needed to be capable of surviving long-term in the high drag and corrosive swimming pool environment. This posed significant issues designing, as I altered the products design such that when fully assembled it was neutrally bouyant, but when split into individual parts, it floated to the surface for easy collection.")]),_vm._v(" "),_c('p',[_vm._v("I started working on this project alongside my A Level studies 2 years ago, since It has featured at several national design competition finals including the Triumph Design Awards and the Big Bang Fair 2019 in Birmingham NEC. I spent last year prototyping further to modify my design to reduce manufacture costs by reeducing materials and production time. I also partially set up a trade deal with a New Zealand Company to manufacture the product with me gaining royalties from sales. Sadly, due to Brexit uncertainties and changes in the Design Right legal framework, this contract closed earlier this year.")]),_vm._v(" "),_c('p',[_vm._v("In response,I designed and built a custom 3D printer capable of printing the parts needed for the product and made jigs to reduce material costs and labour time to 30% of the original cost and 25% of the original manufacturing time.")]),_vm._v(" "),_c('p',[_vm._v("Recently, Flickfiend launched in June 2019, selling around 10 products. We continue to slowly expand into the global market as demand for Flickfiend steadily rises.")]),_vm._v(" "),_c('p',[_vm._v("Further innovation is taking place within Flickfiend, as we next seek to utilize 3D Printing, to undercut the expensive UWH Bat market.")]),_vm._v(" "),_c('p',[_vm._v("Bats currently cost between 60-80 per set, leveraging my Custom 3D Printers Nylon and Carbon Fibre printing capabilities, we can significantly reduce costs.")]),_vm._v(" "),_c('p',[_vm._v("Retailing at half the price of existing similar bats, our bats are stronger, lighter and customised!")]),_vm._v(" "),_c('p',[_vm._v("Flickfiend continues to be a pioneering force in UWH alongside my studies as I strive to bring the sport into the modern era.")])])],1)}
